<template>
  <div class="outline">
    <div class="form">
      <input
        ref="ff"
        type="text"
        class="text_input"
        v-model="user_message"
        @keyup.enter="sendMessage"
      />
    </div>
    <v-btn icon style="color: white;" class="send_btn" @click="sendMessage">
      <v-icon>mdi-arrow-up</v-icon>
    </v-btn>
  </div>
</template>

<script>
import { EventBus } from "../utils/events-bus";

export default {
  data() {
    return {
      user_message: ""
    };
  },
  mounted() {},
  methods: {
    loadMessage() {
      console.log("BB", this.$store.state.messages);
      this.$store.commit("ADD_MESSAGES", {
        message: "Hello World"
      });
      console.log("AA", this.$store.state.messages);

      console.log("load!!!");
      // this.$store.dispatch('LOAD_MESSAGES')
    },
    sendMessage() {
      if (!this.user_message) return;

      const message = this.user_message;

      this.$store.dispatch("SEND_MESSAGE", message).then(error => {
        console.log("RR = ", error);

        if (!error) {
          this.user_message = "";
          // this.$refs.ff.value = "";
          EventBus.$emit("updateList");
        }
      });
      console.log(this.user_message);
    }
  }
};
</script>

<style scoped>
.outline {
  background-color: white;
  display: flex;
}

.form {
  flex: 1;
}

.text_input {
  width: 100%;
  height: 100%;
  padding-left: 10px;
  padding-right: 10px;
  font-family: "Nanum Gothic", sans-serif;
}

input:focus,
input.form-control:focus {
  outline: none !important;
  outline-width: 0 !important;
  box-shadow: none;
  -moz-box-shadow: none;
  -webkit-box-shadow: none;
}

.send_btn {
  background: #3269cd;
  margin: 6px;
}
</style>