import Vue from "vue";
import Vuex from "vuex";

import * as firebase from "firebase/app";
import "firebase/database"

Vue.use(Vuex);

var firebaseConfig = {
  apiKey: "AIzaSyBtnFzSNMp30pWM2CNva-U9ZM1sdE8lzts",
  authDomain: "fir-ca8ad.firebaseapp.com",
  databaseURL: "https://fir-ca8ad.firebaseio.com",
  projectId: "fir-ca8ad",
  storageBucket: "fir-ca8ad.appspot.com",
  messagingSenderId: "371247898053",
  appId: "1:371247898053:web:a657c9fb4898a89947bfac",
  measurementId: "G-BWCDY22QJR"
};
// Initialize Firebase
firebase.initializeApp(firebaseConfig);

export default new Vuex.Store({
  state: {
    hrl_url: '',
    message_timer: null,
    last_timestamp: 0,
    messages: [],
    user_name: ''
  },
  getters: {
    getMessages(state) {
      return state.messages
    },
    getHlsUrl(state) {
      return state.hrl_url
    }
  },
  mutations: {
    ADD_MESSAGES(state, payload) {
      state.messages = state.messages.concat(payload)
    },
    SET_NAME(state) {
      if (!state.user_name) {
        state.user_name = Math.random()
          .toString(36)
          .substring(7);
      }
    },
    SET_HLS_URL(state, payload) {
      state.hrl_url = payload
    }
  },
  actions: {
    LOAD_MESSAGES({ state, commit }) {
      console.log('load message')
      return firebase.database().ref('/messages').orderByChild("time").once('value').then(function (snapshot) {
        const list = []
        snapshot.forEach(function (child) {
          const data = child.val()
          const isMine = data.user === state.user_name
          if (isMine) {
            data.isMine = true
          }
          list.push(data)
        })

        state.last_timestamp = list[list.length - 1].time
        commit('ADD_MESSAGES', list)
        return snapshot
      })
    },
    SET_MESSAGE_EVENT({ state, commit }) {
      state.message_timer = setInterval(function () {
        firebase.database().ref('/messages').orderByChild('time').startAt(state.last_timestamp + 1).once('value').then(function (snapshot) {

          const list = []
          snapshot.forEach(function (child) {
            const data = child.val()
            const isMine = data.user === state.user_name

            if (!isMine)
              list.push(child.val())
          })

          if (list.length > 0) {
            // console.log('UU', list.length, list[0])
            state.last_timestamp = list[list.length - 1].time
            commit('ADD_MESSAGES', list)
          }
        })

      }, 2000)

    },
    DELETE_MESSAGE_EVENT(state) {
      clearTimeout(state.message_timer)
      state.message_timer = null
    },
    SEND_MESSAGE({ commit, state }, payload) {
      const new_key = '/' + firebase.database().ref('/messages').child('data').push().key
      const new_message = {
        user: state.user_name,
        time: new Date().getTime(),
        message: payload
      }
      const updates = {}
      updates['messages/' + new_key] = new_message

      firebase.database().ref().update(updates, function (error) {
        console.log('SEND_RESULT', error)
        if (!error) {
          commit('ADD_MESSAGES', {
            ...new_message,
            isMine: true
          })
        }
        return error
      })
    },
    LOAD_HLS_URL({ commit }) {
      return firebase.database().ref('/').once('value').then(function (snapshot) {
        const hls_url = snapshot.val() && snapshot.val().hrl_url || ''
        commit('SET_HLS_URL', hls_url)

        return hls_url
      })
    }
  },
  modules: {}
});
