<template>
  <div ref="bg" class="tab_bg">
    <v-row>
      <v-col v-for="n in 9" :key="n" class="d-flex child-flex" cols="4">
        <v-img aspect-ratio="1" class="item_bg">
          <v-icon large color="red" class="icon_img">
            mdi-{{ icons[n - 1] }}
          </v-icon>
        </v-img>
      </v-col>

      <v-icon color="yellow" x-large></v-icon>
    </v-row>
  </div>
</template>

<script>
export default {
  props: ["props_height"],
  data() {
    return {
      icons: [
        "cards-heart",
        "emoticon-excited-outline",
        "trophy-outline",
        "emoticon-kiss-outline",
        "human-greeting",
        "emoticon-cool-outline",
        "thumb-up-outline",
        "emoticon-tongue-outline",
        "face-recognition"
      ]
    };
  },
  mounted() {
    this.$refs.bg.style.height = this.props_height + "px";
  }
};
</script>

<style scoped>
.tab_bg {
  padding: 20px;
}

.item_bg {
  background-color: #cadbe940;
  outline: 0.1px solid lightgrey;
}

.icon_img {
  margin-left: -20px;
  height: 100%;
  transform: scale(2, 2);
}
</style>